s Site")]),_c('p',[_vm._v(" 1. Filet may process and use your personal information to authenticate your identity, validate the process that you access or use Filet Services and this Site, facilitate your Orders, execute the Trades between you and other users, develop, enhance, and market Filet Services, provide Filet Services to you, process billing and collect any fees, or as otherwise may be necessary to execute the agreement between you and Filet, or to identify you or to ensure the functionality and security of Filet Services and this Site. The purposes of the aforementioned processing and use are classified as 036 (deposit and remitting), 063 (non-government agency collect or process personal information under legal obligations), 069 (contract, contract-like or other legal relation matter), 090 (consumer, Customer Management and Service), 094 (property management), 135 (information (communication) services), 136 (information (communication) and database management), 148 (internet shopping and other electronic commerce services), 154 (reference), and 181 (other business operation in accordance with the business registration or bylaws) in the Purpose and Classification. ")]),_c('p',[_vm._v(" 2. Development of Filet Services and this Site Filet may process and use your personal information, Content, and/or Feedback to develop, improve, and customize Filet Services and this Site. Filet may also perform research and analytics on your personal information to personalize our offering, for example, to make recommendations and to display customized content and advertising in our websites. The purposes of the aforementioned processing and use are classified as 040 (marketing (including financial cross-selling business)), 090 (consumer, Customer Management and Service), 135 (information (communication) services), 136 (information (communication) and database management), 148 (internet shopping and other electronic commerce services), and 157 (investigation, statistics and