ice and Rights of Your Information Section 2 with regard to your personal information collected, processed, and used by Filet, and you may delete your Filet Account, in which case we may not be able to provide Filet Services and this Site to you. ")]),_c('p',[_vm._v(" You have the right, subject to the applicable laws and Choice and Rights of Your Information Section 3 and Section 4 of this Privacy Policy, ")]),_c('p',[_vm._v(" 1. to inquire or request for a review of the personal information Filet has collected from you; ")]),_c('p',[_vm._v(" 2. to make duplications of the personal information Filet has collected from you; ")]),_c('p',[_vm._v(" 3. to replenish and rectify the personal information Filet has collected from you; ")]),_c('p',[_vm._v(" 4. to request Filet to discontinue our collection, processing and use of the personal information Filet has collected from you; and ")]),_c('p',[_vm._v(" 5. to delete any personal information Filet has collected from you, by yourself or by requesting Filet to do so, in which case we may not be able to provide Filet Services and this Site to you thereafter. Filet cannot, however, delete such personal information that is necessary for compliance with binding legal obligations or if the personal information must be retained according to applicable laws. ")]),_c('p',[_vm._v(" Upon your inquiry or request for a review and making duplication of the personal information Filet has collected from you, Filet shall determine whether to agree to such request within fifteen (15) days from the date on which Filet receives your request, except: ")]),_c('p',[_vm._v(" 1. when the national security, diplomatic and military secrets, the macroeconomic interests or other major national interests may be harmed; ")]),_c('p',[_vm._v(" 2. when the performance of official duties may be interfered with; and ")]),_c('p',[_vm._v(" 3. when the major interests of Filet or a third person may be affected. The fifteen-day period may be extended to a time 